const adduser = 'user/add'
let pageNo=1;
function UserRow(props) {
  const user = props.user
  const userLink = `/users/${user.user.id}`
  const updateuser = `user/update/${user.user.id}`
 

  const getBadge = (status) => {
    return status === 'Active' ? 'success' :
      status === 'Inactive' ? 'secondary' :
        status === 'Pending' ? 'warning' :
          status === 'Banned' ? 'danger' :
            'primary'
  }

  return (
    <tr key={user.user.id.toString()}>
      <th scope="row"><Link to={userLink}>{user.user.id}</Link></th>
      {user.user.is_online==1?
      <td>   
          <div className="avatar">
              <img src={(user.patientProfile==null||user.patientProfile==''||user.patientProfile==undefined)?'assets/img/avatars/1.jpg':baseUrl+user.patientProfile.value} className="img-avatar" alt="admin@bootstrapmaster.com" />
              <span className="avatar-status badge-success"></span>
          </div>
      </td>:
      <td>   
          <div className="avatar">
          <img src={(user.patientProfile==null||user.patientProfile==''||user.patientProfile==undefined)?'assets/img/avatars/1.jpg':baseUrl+user.patientProfile.value} className="img-avatar" alt="admin@bootstrapmaster.com" />
 <span className="avatar-status badge-danger"></span>
          </div>
      </td>
      }
      <td><Link to={userLink}>{user.user.first_name}</Link></td>
      <td>{user.user.middle_name}</td>
      <td>{user.user.email}</td>
      <td><Link to={updateuser}><span class="cui-pencil" aria-hidden="true"></span></Link></td>
    </tr>
  )
}

